:root {
  --bg: #f5f4ec;
  --text: #3d3d3d;
  --text-light: #fff;
  --color-primary: #db3333;
  --color-secondary: pink;
  --color-tertiary: green;
  --color-light: #fff;
  --color-dark: #111;
  --radius: 10px;
  --maxw: 72rem;
  --vertical-gap-content: clamp(.75rem, 1.5vw, 1.5rem);
  --content-gap: clamp(12px, 4vw, 40px);
  --gutter: clamp(16px, 4vw, 48px);
  --section-space: clamp(24px, 6vw, 80px);
  --flow: clamp(48px, 6vw, 120px);
}

html {
  box-sizing: border-box;
  background-color: var(--bg);
  scroll-behavior: smooth;
  height: -webkit-fill-available;
  font-size: 16px;
  line-height: 1.5;
  overflow: hidden auto;
}

*, :before, :after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

body {
  height: 100%;
  color: var(--text);
  background: var(--bg);
  margin: 0;
  font-family: Inter, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
}

img, svg, picture {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5 {
  text-wrap: balance;
  word-wrap: break-word;
  font-family: DM Serif Display, serif;
  font-weight: 700;
  line-height: 1.25;
}

p, li {
  letter-spacing: .02em;
  text-wrap: balance;
  word-wrap: break-word;
  max-width: 65ch;
  font-size: max(1rem, min(1vw + .3rem, 1.1rem));
  line-height: 1.5;
}

h1 {
  font-size: max(2rem, min(5vw + .5rem, 4rem));
}

h2 {
  font-size: max(1.8rem, min(4vw + .5rem, 3.2rem));
}

h3 {
  font-size: max(1.6rem, min(3vw + .4rem, 2.6rem));
}

h4 {
  font-size: max(1.4rem, min(2.5vw + .3rem, 2rem));
}

h5, #title-header {
  font-size: max(1.3rem, min(2vw + .3rem, 1.6rem));
}

header {
  z-index: 10;
  color: #fff;
  background-color: #0000004d;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1.5rem 2rem;
  transition: background-color .3s, -webkit-backdrop-filter .3s, backdrop-filter .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

header.scrolled {
  backdrop-filter: blur(12px);
  background-color: #000000b3;
}

nav a {
  color: inherit;
  margin-left: 1rem;
}

nav a:hover {
  text-decoration: underline;
}

@media (width <= 540px) {
  header {
    gap: var(--vertical-gap-content);
    flex-direction: column;
  }
}

.hero {
  background-blend-mode: multiply;
  background: #0000004d url("Background_plants.e4847a12.jpg") center / cover no-repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.home {
  height: 100vh;
}

.secondary {
  height: 40vh;
}

.hero__content {
  text-align: center;
  max-width: 85ch;
  color: var(--text-light);
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding-left: 2rem;
  padding-right: 2rem;
  display: flex;
}

.container {
  max-width: var(--maxw);
  padding-inline: var(--gutter);
  padding-block: var(--flow);
  margin-left: auto;
  margin-right: auto;
}

.container_content {
  align-items: center;
  gap: var(--content-gap);
  display: flex;
}

.container_content.right {
  flex-direction: row;
  padding-bottom: .5em;
}

.container_content.left {
  flex-direction: row-reverse;
  padding-bottom: .5em;
}

@media (width <= 768px) {
  .container_content.right, .container_content.left {
    flex-direction: column;
  }
}

.content__media {
  background: var(--color-secondary);
  color: var(--text);
  grid-template-columns: 1fr;
  align-items: center;
  display: grid;
}

.content__figure {
  z-index: 1;
  position: relative;
}

.figure {
  aspect-ratio: 4 / 5;
  margin: 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 24px #00000014;
}

.content__content {
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.5rem;
  max-width: 85ch;
  display: flex;
}

.btn {
  letter-spacing: .02rem;
  border-radius: var(--radius);
  color: var(--color-primary);
  cursor: pointer;
  background: none;
  border: 1px solid;
  margin-top: .5rem;
  padding: .9rem 1.5rem 1.2rem;
  font-size: max(1rem, min(1vw + .3rem, 1.25rem));
  font-weight: 500;
  transition: -webkit-transform .25s, transform .25s, box-shadow .25s, background-color .25s, color .25s;
  display: inline-block;
}

.btn--light {
  color: var(--color-light);
  border-color: var(--color-light);
}

.btn--light:hover {
  background: var(--color-light);
  color: var(--color-dark);
}

.btn--dark {
  color: var(--color-primary);
  border-color: var(--color-primary);
}

.btn--dark:hover {
  background: var(--color-primary);
  color: var(--color-light);
}

.btn:hover {
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
  box-shadow: 0 2px 6px #00000026;
}

.title {
  padding-bottom: 2rem;
}

.grid_rincones {
  background: var(--bg);
  padding-top: 0;
}

.grid {
  gap: var(--content-gap);
  grid-template-columns: 1fr;
  display: grid;
}

@media (width >= 640px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card__link {
  transition: -webkit-transform .4s, transform .4s, box-shadow .4s;
  display: block;
}

.card {
  gap: var(--vertical-gap-content);
  border-radius: var(--radius);
  background: var(--color-light);
  flex-direction: column;
  height: 100%;
  padding: 1rem;
  text-decoration: none;
  transition: -webkit-transform .4s, transform .4s, box-shadow .4s;
  display: flex;
}

.card__media {
  border-radius: var(--radius);
  overflow: hidden;
}

.card__media img {
  aspect-ratio: 1;
  object-fit: cover;
  width: 100%;
  height: auto;
  display: block;
}

.card h3 {
  color: var(--text);
  margin: 0;
}

.card p {
  color: var(--text);
  margin: 0;
  line-height: 1.6;
}

.card__link:hover {
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
  box-shadow: 0 12px 28px #00000026;
}

.site-footer {
  background: var(--color-dark);
  color: var(--color-light);
  padding-top: 2rem;
  padding-bottom: 1rem;
  font-size: .95rem;
}

.footer__content {
  max-width: var(--maxw);
  padding-inline: var(--gutter);
  justify-content: space-between;
  align-items: flex-end;
  gap: 2rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.footer__brand {
  max-width: 40rem;
}

.footer__title {
  margin-bottom: .5rem;
  font-size: 1.3rem;
  font-weight: 700;
}

.footer__tagline {
  color: #ffffffb3;
  line-height: 1.4;
}

.footer__list {
  flex-wrap: wrap;
  gap: 1rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.footer__list a {
  color: var(--color-light);
}

@media (width <= 768px) {
  .footer__content {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }
}

.skip-link {
  color: #fff;
  z-index: 1000;
  background: #000;
  border-radius: 8px;
  outline: none;
  padding: .6rem 1rem;
  text-decoration: none;
  transition: -webkit-transform .2s, transform .2s;
  position: absolute;
  top: .5rem;
  left: .5rem;
  -webkit-transform: translateY(-150%);
  transform: translateY(-150%);
}

.skip-link:focus, .skip-link:focus-visible {
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.skip-link:hover {
  text-decoration: underline;
}

@media (prefers-reduced-motion: reduce) {
  .skip-link {
    transition: none;
  }
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  position: absolute !important;
}

:focus-visible {
  outline: 3px solid var(--color-primary);
  outline-offset: 4px;
  border-radius: var(--radius);
  scroll-margin-top: 6rem;
  transition: outline .2s;
}

button:focus-visible, a:focus-visible, input:focus-visible, textarea:focus-visible {
  outline: 3px solid var(--color-primary);
  outline-offset: 3px;
  border-radius: var(--radius);
}
